import React from 'react';
import './App.css';

import { ButtonGrid } from '@musicenviro/ui-elements';
import { Player } from './player/soundfont-player';

class App extends React.Component {
	
	midiSounds: any;
	player = new Player();

	componentDidMount() {
		this.player.init()
	}

	handleButton(row: number, column: number) {

		const index = (3 - row) * 4 + column;
		this.player.playNote(index + 36)
		
	}

	render() {

	return (
		<div className="App">

			<header>hotloads</header>
			<div id="main">
				<ButtonGrid onButtonDown={(row, col) => this.handleButton(row, col)}/>
			</div>
		</div>
	)}
}

export default App;
